@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
input{font-family:"Poppins",sans-serif;width:60%;padding:15px;text-align:left;border-radius:10px;border:none}.table{margin-left:auto;margin-right:auto;border-collapse:collapse;border-radius:1em;overflow:hidden}.table tbody{cursor:pointer;background-color:#d3d3d3}.table th{text-align:left;padding:30px;background-color:#2bb297}.table td{padding:30px}.table tbody:nth-of-type(odd){background-color:#fff}
body{margin:0;padding:0;font-family:"Poppins",sans-serif}.page-container{display:flex;flex-direction:column;align-items:center;justify-content:space-evenly;height:100vh;width:100vw}
.user-container{display:flex;flex-direction:column;align-items:center;border-radius:25px;width:80%}.user-container .user-info-header{background-color:#2bb297;margin-bottom:50px}.user-container .address{background-color:#d3d3d3;margin-bottom:50px}
.app{display:flex;justify-content:center;padding:50px;background-color:#FCD31C21;background-image:url(https://www.configur.tech/wp-content/uploads/2020/10/digital_agency_hero_bg_01.png);height:100vh}
